  line-height: 1;
  font-weight: normal;
  color: #ff6038;
  text-decoration: none;
  user-select: none;
}

.title-sect:after {
  content: "";
  flex: 1;
  margin-left: 19px;
  background: url(../img/pattern.png) 0 0;
}

.slider {
  position: relative;
  overflow: hidden;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 50%;
  left: 20px;
  z-index: 99;
  margin-top: -27px;
  font-size: 54px;
  cursor: pointer;
}

.slider__next {
  top: 46%;
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

.slide {
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 44.8%;
}

.slide__title {
  margin-bottom: 16px;
  font-family: bebas-font,sans-serif;
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}

.slide p {
  font-size: 12px;
  line-height: 1.4;
  color: #ffffff;
}

.article {
  position: relative;
}

.article::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 98.6%;
  height: 13px;
  background: url(../img/pattern.png) 0 0;
}

@media only screen and (max-width: 959px) {
  .article__decor {
    margin-bottom: 10px;
    background-color: rgba(255, 179, 156, 0.5);
  }
}

.article__icon {
  position: absolute;
  right: 100%;
  top: 0;
  width: 75px;
  height: 75px;
  margin-right: 10px;
  background: #ff6038;
  background-image: url(../img/logo.png);
  background-position: center;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 1115px) {
  .article__icon {
    position: relative;
    right: auto;
    display: block;
  }
}

.article__media {
  margin-top: 55px;
  margin-bottom: 17px;
}

.article__link {
  display: block;
  margin-top: 3px;
  margin-bottom: 19px;
}

.article__link--cite {
  margin-bottom: 5px;
}

.article__detail {
  display: flex;
}

@media only screen and (max-width: 959px) {
  .article__detail {
    flex-wrap: wrap;
  }
}

.article__info {
  width: 22%;
  padding-right: 15px;
}

@media only screen and (max-width: 959px) {
  .article__info {
    width: 100%;
    padding-right: 0;
    order: 1;
  }
}

.article__descr {
  width: 78%;
}

@media only screen and (max-width: 959px) {
  .article__descr {
    width: 100%;
  }
}

.article__descr--full {
  width: 100%;
}

.article__text p {
  margin-bottom: 17px;
}

.article__text {
  font-size: 12px;
  line-height: 1.5;
  padding-right: 12px;
}

.article__text a {
  color: #ff6038;
}

.article__cite {
  display: block;
  margin-bottom: 19px;
  font-size: 12px;
  line-height: 1.5;
  color: #a0a0a0;
  text-decoration: none;
}

.article__cite:hover {
  text-decoration: underline;
}

.title-info {
  display: block;
  color: #ff6038;
  text-decoration: none;
}

.title-info:hover .title-info__text {
  opacity: 0.5;
}

.title-info__text {
  font-family: bebas-font,sans-serif;
  font-size: 18px;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  color: currentColor;
}

.info-block__title {
  margin-bottom: 4px;
  font-family: bebas-font,sans-serif;
  font-size: 12px;
  line-height: 1.66;
  color: #ff6038;
  text-transform: uppercase;
  text-decoration: none;
}

.info-block__descr {
  display: block;
  margin-bottom: 9px;
  font-size: 12px;
  line-height: 1.66;
  color: #bababa;
}

.info-block__tag {
  position: relative;
  padding-right: 15px;
  color: #bababa;
  text-decoration: none;
}

.info-block__tag_last-child {
  padding-right: 0;
}

.info-block__tag:hover {
  text-decoration: underline;
}

.tag__link {
  display: inline-block;
}

.tag__link::before {
  content: "|";
  position: absolute;
  right: 6px;
  bottom: 0;
}

.tag__link:last-child::before {
  display: none;
}

.quote {
  position: relative;
  margin-bottom: 26px;
  padding: 21px 15px 20px 76px;
  background-color: #f3f3f3;
  background-image: url(../img/pattern-quote.png);
}

.quote::before {
  content: "";
  position: absolute;
  left: 37px;
  top: 16px;
  width: 25px;
  height: 20px;
  background-image: url(../img/icon-quote.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.quote__text {
  font-style: italic;
  font-size: 13px;
  line-height: 1.38;
  color: #474747;
  text-decoration: none;
}

.pagination {
  display: flex;
}

.pagination__item {
  height: 26px;
  width: 26px;
  margin-right: 4px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  background: #ffb39c;
}

.pagination__item.is-active {
  background: #ff6038;
}

.pagination__link {
  font-size: 14px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
}

.thumbs {
  overflow: hidden;
  position: relative;
  margin-right: -6px;
}

.thumbs__btn-wrap {
  display: flex;
  margin-bottom: 5px;
}

.thumbs__btn {
  display: flex;
  width: 24px;
  margin-right: 4px;
  padding: 5px;
  background-color: #ff6038;
  cursor: pointer;
}

.thumbs__btn.slick-disabled {
  opacity: 0.5;
}

.thumbs__btn .icon-arrow {
  width: 100%;
}

.thumbs__btn--next {
  transform: rotate(-180deg);
}

.thumbs__list {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  width: 100%;
}

.thumbs__link {
  display: block;
  margin-right: 6px;
  margin-bottom: 3px;
  border: 1px solid #ff6038;
  transition: opacity 0.3s;
}

.thumbs__link img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thumbs__link[href]:hover {
  border-color: #f3af9d;
  opacity: 0.8;
}

.categories__link {
  display: block;
  color: #464646;
  text-decoration: none;
  transition: transform 0.3s;
}

.categories__link:hover {
  text-decoration: underline;
  transform: translateX(2%);
}

.video-responsive {
  padding: 0;
  padding-bottom: 45%;
  position: relative;
  display: block;
  height: 0;
  overflow: hidden;
}

.video-responsive__iframe {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.footer-page {
  padding: 20px;
}

@media only screen and (min-width: 960px) {
  .js-animated {
    transition: transform 1.2s cubic-bezier(0.165, 0.84, 0.44, 1) 0s, opacity 0.8s linear 0.1s;
    transform: translate3d(0px, 80px, 0);
    opacity: 0;
  }
  .js-animated.appeared {
    transform: translate3d(0px, 0px, 0px) rotateX(0) scale(1, 1);
    opacity: 1;
  }
}

.nav-trigger {
  display: none;
  position: fixed;
  width: 36px;
  height: 36px;
  right: 0px;
  top: 0px;
  z-index: 200;
  margin: 5px 10px 5px;
  color: #ff6038;
  cursor: pointer;
}

@media only screen and (max-width: 959px) {
  .nav-trigger {
    display: block;
  }
}

.nav-trigger.is-active .nav-trigger__line {
  background-color: transparent;
}

.nav-trigger.is-active .nav-trigger__line::before {
  transform: translateY(0) rotate(45deg);
}

.nav-trigger.is-active .nav-trigger__line::after {
  transform: translateY(0) rotate(-45deg);
}

.nav-trigger__line {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 3px;
  width: 100%;
  background-color: currentColor;
  transition: transform 0.3s, opacity 0.3s;
}

.nav-trigger__line::before, .nav-trigger__line::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 3px;
  width: 100%;
  background-color: currentColor;
  transition: transform 0.3s, opacity 0.3s;
}

.nav-trigger__line::before {
  transform: translateY(-10px);
}

.nav-trigger__line::after {
  transform: translateY(10px);
}

