.canvas {
  position: relative;
  width: 800px;
  height: 600px;
  margin-top: 110px;
}

.canvas > * {
  position: absolute;
  width: 800px;
  height: 600px;
  left: 0;
  top: 0;
  transition: all 0.55s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

body {
  overflow: hidden;
}

body.is-load {
  overflow: visible;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  background-color: #d3ebff;
}

#clouds {
  background: url("../img/cloud_bg.png") repeat-x 0 bottom #d3ebff;
  width: 100%;
  height: 100px;
  min-height: 100px;
  margin-bottom: 50px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transform: translate3d(0, 0, 0.01);
}

@media only screen and (max-width: 959px) {
  #clouds {
    display: none;
  }
}

.text-anim {
  font-family: bebas-font,sans-serif;
  font-size: 60px;
  line-height: 1;
  color: #00943e;
  opacity: 0;
}

.letter-b {
  font-family: bebas-font,sans-serif;
  font-size: 122px;
  color: #ff6038;
  opacity: 0;
}

.star {
  background: #FF655E;
  box-shadow: 0 0 10px 5px #591239;
  border-radius: 100%;
  position: absolute;
}
